@import url(https://fonts.googleapis.com/css2?family=Kufam:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #6699ff;
  font-family: 'Kufam';
  overflow-x: none;
}


.grid-container {
  display: grid;
  grid-template-columns: 10% 60% 29%;
  grid-template-rows: 15vh 90vh 5vh;
  grid-gap: 5px;
  gap: 5px;
  grid-template-areas:
  "Top Top Top"
  "Free-space Main-Frame Sidebar"
  "Bottom-bar Bottom-bar Bottom-bar";
  background-color: #6699ff;
  
  
}

.Top { 
  padding-top: 0.5em;
  font-family: 'Kufam';
  text-align: center;
  color:white;
  font-size: 50px;
  grid-area: Top; 
  background: linear-gradient(
    90deg, 
  rgba(2,0,36,1) 0%, 
  rgba(9,120,121,1) 38%, 
  rgba(0,212,255,1) 100%);
}

.Main-Frame { 
  font-family: 'Kufam';
  border: 3px solid #830c0c;
  border-radius: 50px;
  grid-area: Main-Frame; 
  background: rgb(9,120,121);
  background: radial-gradient(circle, rgba(9,120,121,1) 95%, rgba(0,212,255,1) 100%);
  overflow: scroll;
  overflow-x: hidden;
}

.Main-Frame::-webkit-scrollbar{
  display: none;
}

.Sidebar { 
  font-family: 'Kufam';
  border-radius: 10px;
  grid-area: Sidebar; 
  background-color: green;
}

.Free-space { 
  grid-area: Free-space; 
  background-color: #6699ff;
}

.Bottom-bar { 
  grid-area: Bottom-bar; 
  background-color: #6699ff;
  width: 101%;
}

.HomePage {
    display: block;
    text-align: center;
    padding-top: 50px;
    font-size: 30px;
    color: white;
    -webkit-text-stroke: 2% black;
}

.Article {
    align-content: center;
}

.ArticleHeader {
    text-align: center;
    color:white;
    text-shadow: black;
}

.ArticleDate {
    text-align: center;
    color:white;
    text-shadow: black;
}

.ArticleImage {
    border-radius: 25px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 40%;
}

.ArticleText {
    
    padding-left: 5%;
    padding-right: 5%;
    text-indent: 5em;
    color:white;
    text-shadow: black;
    border-color: black;
    border-style: none none solid none;
}
.AboutUs {
    padding-top: 2%;
    padding-left: 5%;
    color: white;
    list-style: square;
    -webkit-text-stroke: 2% black;
    font-size: 25px;
}
.ContactUs {
    padding-top: 2%;
    padding-left: 5%;
    color: white;
    list-style: square;
    -webkit-text-stroke: 2% black;
    font-size: 25px;
}
.Sidebar {
    display: grid;  
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-gap: 5px;
    gap: 5px;
    text-align: center;
    padding-top: 5%;
    padding-bottom: 5%;
    border-collapse: separate;
    border-spacing: 5px;
}

