.Sidebar {
    display: grid;  
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    gap: 5px;
    text-align: center;
    padding-top: 5%;
    padding-bottom: 5%;
    border-collapse: separate;
    border-spacing: 5px;
}

.Home {
    text-decoration: none;
    margin-left: 5px;
    margin-right: 5px;
    border-radius: 10px;
    font-size: 30px;
    color: white;
    -webkit-text-stroke: 2% black;
    background-color:rgb(134, 17, 17);
    transition: 0.1s;
    
}

.Home:hover {
    background-color:rgb(155, 18, 18);
    
}

