@import url('https://fonts.googleapis.com/css2?family=Kufam:ital@1&display=swap');

.App {
  text-align: center;
  background-color: #6699ff;
  font-family: 'Kufam';
  overflow-x: none;
}


.grid-container {
  display: grid;
  grid-template-columns: 10% 60% 29%;
  grid-template-rows: 15vh 90vh 5vh;
  gap: 5px;
  grid-template-areas:
  "Top Top Top"
  "Free-space Main-Frame Sidebar"
  "Bottom-bar Bottom-bar Bottom-bar";
  background-color: #6699ff;
  
  
}

.Top { 
  padding-top: 0.5em;
  font-family: 'Kufam';
  text-align: center;
  color:white;
  font-size: 50px;
  grid-area: Top; 
  background: linear-gradient(
    90deg, 
  rgba(2,0,36,1) 0%, 
  rgba(9,120,121,1) 38%, 
  rgba(0,212,255,1) 100%);
}

.Main-Frame { 
  font-family: 'Kufam';
  border: 3px solid #830c0c;
  border-radius: 50px;
  grid-area: Main-Frame; 
  background: rgb(9,120,121);
  background: radial-gradient(circle, rgba(9,120,121,1) 95%, rgba(0,212,255,1) 100%);
  overflow: scroll;
  overflow-x: hidden;
}

.Main-Frame::-webkit-scrollbar{
  display: none;
}

.Sidebar { 
  font-family: 'Kufam';
  border-radius: 10px;
  grid-area: Sidebar; 
  background-color: green;
}

.Free-space { 
  grid-area: Free-space; 
  background-color: #6699ff;
}

.Bottom-bar { 
  grid-area: Bottom-bar; 
  background-color: #6699ff;
  width: 101%;
}
